.profile {
    max-width: 880px;
    margin: 0 auto;
    padding-top: 40px;
    color: #fff;
    display: flex;
    align-items: center;
    position: relative;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

@media screen and (max-width:1024px) {
    .profile {
        max-width: 282px;
        padding-top: 42px;
        flex-direction: column;
    }
}