.profile__button-add {
    width: 150px;
    height: 50px;
    background: url(../../../images/plus.svg) no-repeat;
    border: 2px solid #fff;
    padding: 0;
    border-radius: 2px;
    margin-left: auto;
    background-position: center;
    cursor: pointer;
}

.profile__button-add:hover {
    opacity: .6;
}

@media screen and (max-width:1024px) {
    .profile__button-add {
        width: 282px;
        margin-top: 33px;
        background-size: 16px;
    } 
}