.popup {
    display: flex;
    position: fixed;
    width: 100%;
    height: 100vh;
    right: 0;
    top: 0;
    background:  rgba(0, 0, 0, .5);
    align-items: center;
    justify-content: center;
    opacity: 0;
    visibility: hidden;
    transition: visibility 150ms, opacity 300ms ease-in-out;
}