.popup__button-close {
  border: none;
  position: absolute;
  right: -40px;
  top: -40px;
  width: 32px;
  height: 32px;
  background-color: transparent;
  background-image: url('../../../images/Close-Icon.svg');
  cursor: pointer;
}

@media screen and (max-width: 1024px) {
  .popup__button-close {
    right: 0px;
    top: -36px;
    width: 20px;
    height: 20px;
    background-size: cover;
  }
}

.popup__button-close:hover {
  opacity: .6;
  transition: opacity .3s ease;
}